import React, { useState, useEffect } from "react"
import { Layer, List, Select, Box, Button, Text } from 'grommet';
import { formation } from '../cm-lib/reference/Formation'
import { DIRECTION } from '../cm-lib/constants'
import Bazi from '../components/Bazi'
import QiMenChart from '../components/QiMen'
import { useTranslation } from 'react-i18next';
import { Adsense } from '@ctrl/react-adsense';

const Home = ({ chartType, onClick, chart, initialDate, onDateChanged, date, handleSearch, onSquarePressed }) => {
  const [selectedDate, setSelectedDate] = useState(date || new Date())
  const [qimenChart, setQimenChart] = useState({})
  const isYangChart = chart && chart.chart && chart.structure.search('Yang') > -1 || chart && chart.chart && chart.structure.search('M') > -1
  useEffect(() => {
    onDateChanged && onDateChanged(selectedDate, setQimenChart)
  }, [selectedDate])

  return <>
    
    <Box
        direction="row"
    >
        <Box alignSelf="start"> <Text background='brand'> openfengshui.com</Text> </Box>
        <Box margin={{ left: 'auto' }}> <Text background='brand'>{ isYangChart ? 'External' : 'Internal' }</Text> </Box>
    </Box>
    <QiMenChart chartType={chartType} chart={chart || qimenChart} onSquarePressed={onSquarePressed} />
    <Box> <Text background='brand'>{ isYangChart ? 'Internal' : 'External' }</Text> </Box>
    <Adsense
        client="ca-pub-6343289823091285"
        slot="5114483936"
    />
  </>
}

export default Home
