import React, { useState, useEffect } from "react"
import { Box } from 'grommet';
import styled from 'styled-components'
import Square from '../Square'
import Bazi from '../Square/Bazi'
import BaziHeader from '../Square/Header'
import { dateObjectToBazi } from '../../cm-lib/FourPillar'

const BoxWrapper = styled(Box)`
  width: 25%;
`

const Home = ({ inputDate, onBazi, luckPillar, list }) => {

  const [chart, setChart] = useState([]);
  const [baziObject, setBaziObject] = useState(null)
  useEffect(() => {
    const baziChart = dateObjectToBazi(inputDate || new Date());
    setChart([
      { symbol: baziChart.GanZhiYear.ganzhi.substr(0, 1) },
      { symbol: baziChart.GanZhiMonth.ganzhi.substr(0, 1) },
      { symbol: baziChart.GanZhiDay.ganzhi.substr(0, 1) },
      { symbol: baziChart.GanZhiHour.ganzhi.substr(0, 1) },
      { symbol: baziChart.GanZhiYear.ganzhi.substr(1, 1) },
      { symbol: baziChart.GanZhiMonth.ganzhi.substr(1, 1) },
      { symbol: baziChart.GanZhiDay.ganzhi.substr(1, 1) },
      { symbol: baziChart.GanZhiHour.ganzhi.substr(1, 1) },
    ])
    onBazi && onBazi(baziChart)
    setBaziObject(baziChart)
  }, [inputDate])
  return <>



    <Box
      direction="row"
    >
    {
      list && list.map(e => <Box fill="horizontal"> { e } </Box>)  
    }
    </Box>
    
    {
      !list && <>
      <Box 
    background="neutral-3"
    alignSelf="center"
    justify="center"
    direction="row"
    wrap={true}
    fill="horizontal"
  >

  
  {
    ['Year', 'Month', 'Day', 'Hour'].map((e, i) => <BoxWrapper
      border={true}
    >
    <Square data={{
        symbol: e
      }} 
      Template={BaziHeader} 
      inline/>
    </BoxWrapper>)
  }
  </Box>
  <Box 
    background="neutral-3"
    alignSelf="center"
    justify="center"
    direction="row"
    wrap={true}
    fill="horizontal"
  >
  
  {
    chart && chart.map((e, i) => <BoxWrapper
      border={true}
    >
    <Square data={{
        structure: chart.structure,
        ...e,
        index: i
      }} Template={Bazi} />
    </BoxWrapper>)
  }
  </Box >
  <br />      
  </>
  }
  </>
}

export default Home
